/**
 * 
 *
 * @param {*} time .
 * @param {String} fmt .
 * @returns {String} .
 */
export function dateFormat(time,fmt) {
  var date = new Date(time);
  var o = {
    "M+": date.getMonth() + 1, // 
    "d+": date.getDate(), // 
    "h+": date.getHours(), // 
    "m+": date.getMinutes(), // 
    "s+": date.getSeconds(), // 
    "q+": Math.floor((date.getMonth() + 3) / 3), // 
    "S": date.getMilliseconds() // 
  };
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
  for (var k in o)
  if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  return fmt;
}

/**
 * 
 *
 * @param {*} time .
 * @returns {String} .
 */
export function dateBeautify(str) {
  var date = new Date(str);
  var now = new Date();
  var time = now.getTime() - date.getTime(); //- =  = 
  if(now.getFullYear()>date.getFullYear()){
    return `${date.getFullYear()}${date.getMonth()+1}${date.getDate()}`
  }else if(now.getFullYear() == date.getFullYear()){
    if(now.getMonth() == date.getMonth()){
      if(now.getDate() == date.getDate()){
        if (time / 1000 < 60) {
          return '';
        } else if ((time / 60000) < 60) {
          return parseInt((time / 60000),10) + '';
        }
        return `${date.getHours()<10?'0'+date.getHours():date.getHours()}:${date.getMinutes()<10?'0'+date.getMinutes():date.getMinutes()}`;
      }else if(now.getDate() == date.getDate()+1){
         return `${date.getHours()<10?'0'+date.getHours():date.getHours()}:${date.getMinutes()<10?'0'+date.getMinutes():date.getMinutes()}`;
      }else if(now.getDate() == date.getDate()+2){
        return `${date.getHours()<10?'0'+date.getHours():date.getHours()}:${date.getMinutes()<10?'0'+date.getMinutes():date.getMinutes()}`;
      }else{
        return `${date.getMonth()+1}${date.getDate()}`;
      }
    }else{
      return `${date.getMonth()+1}${date.getDate()}`;
    }
  }
}

/**
 * 
 *
 * @param {Number} time .
 * @returns {String} .
 */
export function formatSongTime(time) {
  var minute = Math.floor(time / 60);
  var second = Math.floor(time - (minute * 60))
  if(minute < 10) {
    minute = "0" + minute
  }
  if(second < 10) {
    second = "0" + second
  }
  time = minute + ":" + second
  return time
}


