import provinces from './config/provinces'
import cities from './config/cities'

/**
 * 
 *
 * @param {Number} num .
 * @returns {Number|String} .
 */
export function numberFormat(num) {
	num = parseInt(num,10);
	if(num <= 99999) {
		return num
	}
	num = Math.round(parseInt(num/10000,10));
	return `${num}`
}

/**
 * 
 *
 * @param {Number} value .
 * @param {Number} type 12.
 * @returns {Number|String} .
 */
export function areaParse(value,type) {
  let currArea;
  if(type === 1) {
    currArea = provinces.filter(i => i.code == value)
  }else if(type === 2) {
    currArea = cities.filter(i => i.code == value)
  }
  if(!currArea.length) {
    return null
  }
  return currArea[0].name;
}

/**
 *
 *@param {string} data 
 *@return {array} 
 *
 */
export function parseLrc(data) {
  var pattern = /\[\d{2}:\d{2}.\d{2,}\]/g;
  if(!pattern.test(data)) {
    return [];
  }
  var lines = data.split('\n');
  
  var result = [];
  while(!pattern.test(lines[0])) {
    lines = lines.slice(1)
  };
  for(var i = 0; i < lines.length; i++) {
    if(lines[i] == "" || typeof(lines[i]) == "undefined") {
      delete lines[i];
    }
  }
  lines.forEach(function(item, index, data) {
    var time = item.match(pattern);
    var value = item.replace(pattern, '');
    time.forEach(function(v, i, a) {
      var t = v.slice(1, -1);
      t = t.split(':');
      result.push([parseInt(t[0], 10) * 60 + parseFloat(t[1]), value]);
    })
  })
  result.sort(function(a, b) {
    return a[0] - b[0];
  });
  return result
};

/**
 * url
 *@param {string} name 
 *@return {array} 
 *
 */
export function getUrlParam(name) {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //
  var r = window.location.search.substr(1).match(reg); //
  if(r != null) return unescape(r[2]);
  return null; //
}

/**
 *
 *@param {function} fn 
 *@param {number} delay 
 *@return {function} 
 *
 */
export function debounce(fn,delay) {
  var last
  return function(){
    var ctx = this, args = arguments
    clearTimeout(last)
    last = setTimeout(function(){
        fn.apply(ctx, args)
    }, delay)
  }
}



